import idb from "idb";
import { StitchClientFactory } from "mongodb-stitch";
//CacheService allows to fetch data from the Stitch database and caches/updates it if necessary.
class CacheService {
  constructor() {
    this.client = StitchClientFactory.create("weathertest-urceu");
    //check for support
    if (!("indexedDB" in window)) {
      console.log("This browser doesn't support IndexedDB");
      return null;
    }
    //check for database
    this.dbPromise = idb.open("WeatherPWA1", 1, function(upgradeDb) {
      if (!upgradeDb.objectStoreNames.contains("stations")) {
        upgradeDb.createObjectStore("province", {
          keyPath: "province"
        });
      }
      if (!upgradeDb.objectStoreNames.contains("stationData")) {
        upgradeDb.createObjectStore("stationData", {
          keyPath: "station"
        });
      }
      if (!upgradeDb.objectStoreNames.contains("stationStats")) {
        upgradeDb.createObjectStore("stationStats", {
          keyPath: "station"
        });
      }
    });

    this.init = this.init.bind(this);
    this.getProvinceStations = this.getProvinceStations.bind(this);
    this.getStationData = this.getStationData.bind(this);
    this.last12Months = this.last12Months.bind(this);
  }

  init() {
    return new Promise((resolve, reject) =>
      this.checkConnection().then(
        ok => {
          this.db = ok;
          resolve("connection ok");
        },
        error => {
          this.db = false;
          reject("connection not made, using cache...");
        }
      )
    );
  }

  //check connection to stitch
  checkConnection() {
    return new Promise((resolve, reject) =>
      this.client
        .then(stitchClient => {
          this.client = stitchClient;
          this.client
            .authenticate(
              "apiKey",
              "IjJKAehxDRcOWyuH1qsESuiWJS4AJU0JeIy9TRUNfADnlSKSMnB12dZcwFpQYbTs"
            )
            .then(userId => {
              var db = this.client
                .service("mongodb", "mongodb-atlas")
                .db("canada");
              resolve(db);
            })
            .catch(err => {
              console.error("Error on auth " + err);
              reject(err);
            });
        })
        .catch(err => {
          console.error("Error authenticating: " + err);
          reject(err);
        })
    );
  }

  getProvinceStations(province) {
    return new Promise((resolve, reject) => {
      this.client.executeFunction("searchStationCache", province).then(
        e => {
          var provinces = {
            province: province,
            list: e
          };
          this.save("province", provinces).then(() => resolve(e));
        },
        error => {
          //an error was made on save or on connection,  i search for cache and resolve if possible
          console.log(
            "No connection was made or there's an error in the promise. Serving from cache."
          );
          this.get("province", province).then(
            found => {
              resolve(found.list);
            },
            error2 => {
              reject(error2);
            }
          );
          //reject(error);
        }
      );
    });
  }

  getStationData(station) {
    return new Promise((resolve, reject) => {
      this.client.executeFunction("stationDetail", station).then(
        e => {
          this.save("stationData", e).then(() => resolve(e));
        },
        error => {
          this.get("stationData", station).then(
            found => {
              resolve(found);
            },
            error2 => {
              reject(error2);
            }
          );
        }
      );
    });
  }

  save(os, item) {
    return new Promise((resolve, reject) => {
      this.dbPromise
        .then(db => {
          var tx = db.transaction(os, "readwrite");
          var store = tx.objectStore(os);
          //console.log(os,item);
          store.put(item);
          return tx.complete;
        })
        .then(
          () => {
            //console.log("saved!");
            resolve("item updated");
          },
          error => {
            reject("couldn't update " + error);
          }
        );
    });
  }

  get(os, item) {
    return new Promise((resolve, reject) => {
      this.dbPromise
        .then(db => {
          var tx = db.transaction(os, "readonly");
          var store = tx.objectStore(os);
          return store.get(item);
        })
        .then(
          val => {
            if(typeof val !== "undefined"){
             // console.log("found!");
             // console.log(val);
              resolve(val);
            }else{
              reject("couldnt find object");
            }
          },
          () => {
            reject("couldnt find object");
          }
        );
    });
  }

  last12Months(s, d) {
    return new Promise((resolve, reject) => {
      this.client.executeFunction("lastTwelveMonths", s, d).then(
        e => {
          var chartData = {
            station: s,
            data: e
          };
          this.save("stationStats", chartData).then(() => resolve(e));
        },
        error => {
          this.get("stationStats", s).then(
            found => {
              resolve(found.data);
            },
            error2 => {
              reject(error2);
            }
          );
        }
      );
    });
  }
}

export default CacheService;



